var fs = require('fs');
var path = require('path');
var Sequelize = require('sequelize');
var basename = path.basename(module.filename);
var env = process.env.NODE_ENV || 'development';
import environment from "../environment.js";

var db = {};

var sequelize = new Sequelize("", environment.config.db.username, environment.config.db.password, { port: 3306, host: environment.config.db.host, dialect: 'mysql' });

sequelize.query(`CREATE DATABASE IF NOT EXISTS ${environment.config.db.name}`).then(() => {
    console.log('db created')
})

module.exports = db;